* {
  margin: 0;
  padding: 0;
}
.hero {
  height: 100vh;
  width: 100%;
  background-color: #ffffff;
  position: relative;
  overflow-x: hidden;
}
.road {
  height: 140px;
  width: 500%;
  display: block;
  background-image: url(../../public/road.svg);
  background-repeat: repeat-x;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  animation: road 5s linear infinite;
}
@keyframes road {
  100% {
    transform: translateX(-3400px);
  }
}
.city {
  height: 820px;
  width: 500%;
  display: block;
  background-image: url(../../public/bg.svg);
  background-repeat: repeat-x;
  position: absolute;
  bottom: 140px;
  left: 0;
  right: 0;
  z-index: 1;
  animation: city 20s linear infinite;
}
@keyframes city {
  100% {
    transform: translateX(-1400px);
  }
}
.car {
  width: 400px;
  left: 50%;
  bottom: 100px;
  transform: translateX(-50%);
  position: absolute;
  z-index: 2;
}
.car_1 {
  width: 60%;
}
.wheels {
  left: 50%;
  bottom: 127px;
  transform: translate(-50px);
  position: absolute;
  z-index: 2;
}
.wheels {
  img {
    width: 37px;
    height: 37px;
    animation: wheels 1s linear infinite;
  }
  .front_wheel {
    position: absolute;
    left: 33px;
  }
  .back_wheel {
    position: absolute;
    left: -130px;
  }
}
@keyframes wheels {
  100% {
    transform: rotate(360deg);
  }
}
.baloon {
  position: absolute;
  z-index: 3;
  top: 5%;
  left: 33%;
  animation: baloon 26s infinite both;
  animation: cloud 6s infinite both;
}
@keyframes baloon {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(100px);
  }
  100% {
    transform: translateX(0px);
  }
}
@keyframes cloud {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(15px);
  }
  100% {
    transform: translateY(0);
  }
}
